@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
.title{text-align:center}

.image-drop{width:100%;height:50px;margin:0 10px;display:flex;flex:1 1;justify-content:center;align-items:center;border:2px dashed gray;cursor:pointer}.image-drop__input{display:none}.image-drop__placeholder{font-weight:bold;font-size:18px}

.payments__input-group{display:flex !important}.payments__select{width:130px}.payments__input{flex:1 1}


*{box-sizing:border-box}.app{width:800px;min-height:100vh !important;padding:10px;margin:0 auto}.ant-col{padding:0 10px}.ant-modal-body{padding:0px !important}.input-group{display:flex;margin:5px 0px}

.card-title{font-family:Raleway,sans-serif;text-transform:uppercase;font-style:normal;font-weight:800;font-size:28px;line-height:33px;color:#F7791B;text-shadow:0px 0px 16px rgba(247,121,27,0.25)}.card-item{height:45px;padding:0px 15px;border-radius:7px;background:rgba(92,84,76,0.6);overflow:hidden}

*{box-sizing:border-box}.afterword-card{position:relative;width:860px;padding:25px;display:flex;flex-direction:column;align-items:center}.afterword-card *{z-index:3}.afterword-card__text{width:100%;margin-bottom:15px;font-family:Raleway,sans-serif;font-style:normal;font-weight:600;font-size:24px;line-height:27px;text-indent:40px;color:#EDE7DF;flex-grow:1}.afterword-card__blur{position:absolute;top:0px;left:0px;width:100%;height:100%;background:rgba(32,30,29,0.9);z-index:2}.afterword-card__bg{position:absolute;top:0px;left:0px;width:100%;height:100%;object-fit:cover;z-index:1}.donators{width:100%;display:flex;flex-direction:column}.donators__item{position:relative;width:100%;margin-bottom:15px;display:flex;justify-content:space-between;align-items:center}.donators__place{position:absolute;top:calc(-30px / 2);left:calc(-30px / 2);width:30px;height:30px;border-radius:25px}.donators__place--1{background-color:#FFDC33}.donators__place--2{background-color:#9D9788}.donators__place--3{background-color:#CD7F32}.donators__name{font-family:Raleway,sans-serif;font-style:normal;font-weight:600;font-size:18px;line-height:21px;color:white}.donators__sum{font-family:Raleway,sans-serif;font-style:normal;font-weight:800;font-size:18px;line-height:21px;text-align:right;color:white}.payments{display:flex;justify-content:space-between;flex-wrap:wrap;grid-gap:15px 30px;gap:15px 30px}.payments__item{display:flex;align-items:center;color:white;flex-grow:1;min-width:390px}.payments__icon{width:25px;fill:white;margin-right:10px}.payments__data{flex:1 1;font-style:normal;font-weight:500;font-size:20px;line-height:23px}.author{position:relative;width:100%;display:flex;justify-content:space-between;align-items:center;margin-top:15px}.author__img{width:60px;height:60px;border-radius:100px}.author__left{display:flex;align-items:center}.author__center{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-family:Roboto,sans-serif;font-style:normal;font-weight:500;font-size:20px;line-height:23px;color:white}.author__data{display:flex;flex-direction:column;margin-left:7px}.author__nickname{font-family:Roboto,sans-serif;font-style:normal;font-weight:500;font-size:20px;line-height:23px;color:#F7791B}.author__tag{font-family:Roboto,sans-serif;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;color:#A49C9C}



